import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { Modal } from 'react-bootstrap';
import './Progressbar.css';
import ProgressBar from 'react-bootstrap/ProgressBar';
import ScheduleBannerComponent from './schedule-banner/ScheduleBannerComponent';

import TableComponent from '../_reusable/table/TableComponent';
import ScheduleTableHeaderComponent from './schedule-table/schedule-table-header/ScheduleTableHeaderComponent';
import ScheduleDataComponent from './ScheduleDataComponent';
import ScheduleService from './ScheduleService';
import { success } from '../../shared/elements/notification/NotifyComponent';
import Styles from './Schedule.module.css';
import ScheduleForms from './ScheduleForms';
import authService from '../../core/auth.service';
import DateService from '../../core/date.service';
import YesNoModalComponent from '../../shared/components/yes-no-modal/YesNoModalComponent';

function ScheduleComponent() {
  const navigate = useNavigate();
  const { state } = useLocation();
  const [channelOptions, setChannelOptions] = useState();
  const [isLoading, setIsLoading] = useState(true);
  const [isLoadingSession, setIsLoadingSession] = useState(true);
  const [filter, setFilter] = useState({
    channelId: 0,
    month: 0,
    year: 0,
  });
  const [schedules, setSchedules] = useState([]);
  const [disabledGenerateTitles, setDisabledGenerateTitles] = useState(true);
  const [disabledGenerate, setDisabledGenerate] = useState(true);
  const [disabledCreate, setDisabledCreate] = useState(true);
  const [paginationParameters, setPaginationParameters] = useState({
    totalCount: 1,
    pageSize: 1,
    currentPage: 1,
    totalPages: 1,
  });
  const [tableParameters, setTableParameters] = useState({
    pageNumber: 1,
    pageSize: 50,
  });
  const [filterModel, setFilterModel] = useState({ userSort: 2 });
  const [sortInfo, setSortInfo] = useState({});
  const [sortBy, setSortBy] = useState([
    {},
    {
      state: true,
    },
    {
      state: false,
    },
    {
      state: false,
    },
    {
      state: false,
    },
    {
      state: false,
    },
    {
      state: false,
    },
    {
      state: false,
    },
    {
      state: false,
    },
    {
      state: false,
    },
    {
      state: false,
    },
    {
      state: false,
    },
  ]);

  const [scoringColumns, setScoringColumns] = useState({
    neatness: false,
    strengthofPrimetime: false,
    averageBreakTime: false,
    youthofAsset: false,
    ukBoxOffice: false,
    usBoxOffice: false,
    adOptimisation: false,
  });
  const [progress, setProgress] = useState(0);
  const [progressText, setProgressText] = useState('');
  const [isGenerating, setIsGenerating] = useState(false);
  const [disabledText, setDisabledText] = useState('');
  const [modalShow, setModalShow] = useState(false);
  const [hiddenSelectChannels, setHiddenSelectChannels] = useState(true);
  const [defaultSelected, setDefaultSelected] = useState({
    channel: 'Please select a channel',
    month: 0,
    year: 0,
  });

  const currentDate = new Date();
  const [period, setPeriod] = useState({
    start: new Date(
      currentDate.getFullYear(),
      currentDate.getMonth(),
      currentDate.getDate()
    ),
    end: new Date(currentDate.getFullYear(), currentDate.getMonth() + 2, 0),
  });
  const range = {
    start: new Date(
      currentDate.getFullYear(),
      currentDate.getMonth(),
      currentDate.getDate()
    ),
    end: new Date(
      currentDate.getFullYear() + 3,
      currentDate.getMonth(),
      currentDate.getDate()
    ),
  };
  const [selectedPeriod, setSelectedPeriod] = useState({});
  const [deleteModalShow, setDeleteModalShow] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const [deleteID, setDeleteID] = useState(0);
  const getChannels = () => {
    ScheduleService.getChannels().then((response) => {
      const channels = response.data;
      const channelList = [];
      channels.map((value) => {
        channelList.push({ id: value.channelId, name: value.channelName });
        return true;
      });
      setChannelOptions(channelList);
      setHiddenSelectChannels(false);
    });
  };

  const setPeriodFilterStartRange = (value) => {
    setSelectedPeriod((prevState) => ({
      ...prevState,
      start: value,
    }));
  };

  const setPeriodFilterEndRange = (value) => {
    setSelectedPeriod((prevState) => ({
      ...prevState,
      end: value,
    }));
  };

  const getTableColumns = (id) => {
    ScheduleService.getChannelScoring(id).then((response) => {
      const { scores } = response.data;
      let neatnessEnabled;
      let strengthofPrimetimeEnabled;
      let averageBreakTimeEnabled;
      let youthofAssetEnabled;
      let ukBoxOfficeEnabled;
      let usBoxOfficeEnabled;
      let adOptimisationEnabled;
      scores.map((value) => {
        switch (value.name) {
          case 'Neatness':
            neatnessEnabled = value.enabled;
            break;
          case 'Strength of Primetime':
            strengthofPrimetimeEnabled = value.enabled;
            break;
          case 'Average Break Time':
            averageBreakTimeEnabled = value.enabled;
            break;
          case 'Youth of Asset':
            youthofAssetEnabled = value.enabled;
            break;
          case 'UK Box Office':
            ukBoxOfficeEnabled = value.enabled;
            break;
          case 'US Box Office':
            usBoxOfficeEnabled = value.enabled;
            break;
          case 'Ad Optimisation':
            adOptimisationEnabled = value.enabled;
            break;
          default:
            break;
        }
        return true;
      });

      setScoringColumns({
        neatness: neatnessEnabled,
        strengthofPrimetime: strengthofPrimetimeEnabled,
        averageBreakTime: averageBreakTimeEnabled,
        youthofAsset: youthofAssetEnabled,
        ukBoxOffice: ukBoxOfficeEnabled,
        usBoxOffice: usBoxOfficeEnabled,
        adOptimisation: adOptimisationEnabled,
      });
    });
  };

  const callBackSelectChannel = (id) => {
    setTableParameters((prevState) => ({
      ...prevState,
      pageNumber: 1,
    }));
    setFilter((prevState) => ({
      ...prevState,
      channelId: id,
    }));
    getTableColumns(id);
  };

  const callBackSelectMonth = (value) => {
    setTableParameters((prevState) => ({
      ...prevState,
      pageNumber: 1,
    }));
    setFilter((prevState) => ({
      ...prevState,
      month: value,
    }));
  };

  const callBackSelectYear = (selected) => {
    setTableParameters((prevState) => ({
      ...prevState,
      pageNumber: 1,
    }));
    setFilter((prevState) => ({
      ...prevState,
      year: selected.name,
    }));
  };

  const constructQueryString = () => {
    const cID = filter.channelId == null ? 0 : filter.channelId;
    const headerParam = `?channelId=${cID}`;
    const idFilterParam =
      filterModel.iDFilter !== undefined
        ? `&iDFilter=${filterModel.iDFilter}`
        : '';
    const iDFilterValueParam =
      filterModel.iDFilterValue !== undefined
        ? `&iDFilterValue=${filterModel.iDFilterValue}`
        : '';
    const weightedFilterParam =
      filterModel.weightedFilter !== undefined
        ? `&weightedFilter=${filterModel.weightedFilter}`
        : '';
    const weightedFilterValueParam =
      filterModel.weightedFilterValue !== undefined
        ? `&weightedFilterValue=${filterModel.weightedFilterValue}`
        : '';

    const neatnessFilterParam =
      filterModel.neatnessFilter !== undefined
        ? `&neatnessFilter=${filterModel.neatnessFilter}`
        : '';
    const neatnessFilterValueParam =
      filterModel.neatnessFilterValue !== undefined
        ? `&neatnessFilterValue=${filterModel.neatnessFilterValue}`
        : '';
    const primetimeFilterParam =
      filterModel.primetimeFilter !== undefined
        ? `&primetimeFilter=${filterModel.primetimeFilter}`
        : '';
    const primetimeFilterValueParam =
      filterModel.primetimeFilterValue !== undefined
        ? `&primetimeFilterValue=${filterModel.primetimeFilterValue}`
        : '';

    const breakFilterParam =
      filterModel.breakFilter !== undefined
        ? `&breakFilter=${filterModel.breakFilter}`
        : '';
    const breakFilterValueParam =
      filterModel.breakFilterValue !== undefined
        ? `&breakFilterValue=${filterModel.breakFilterValue}`
        : '';

    const youthFilterParam =
      filterModel.youthFilter !== undefined
        ? `&youthFilter=${filterModel.youthFilter}`
        : '';
    const youthFilterValueParam =
      filterModel.youthFilterValue !== undefined
        ? `&youthFilterValue=${filterModel.youthFilterValue}`
        : '';

    const ukBoxOfficeFilterParam =
      filterModel.ukBoxOfficeFilter !== undefined
        ? `&ukBoxOfficeFilter=${filterModel.ukBoxOfficeFilter}`
        : '';
    const ukBoxOfficeFilterValueParam =
      filterModel.ukBoxOfficeFilterValue !== undefined
        ? `&ukBoxOfficeFilterValue=${filterModel.ukBoxOfficeFilterValue}`
        : '';

    const usBoxOfficeFilterParam =
      filterModel.usBoxOfficeFilter !== undefined
        ? `&usBoxOfficeFilter=${filterModel.usBoxOfficeFilter}`
        : '';
    const usBoxOfficeFilterValueParam =
      filterModel.usBoxOfficeFilterValue !== undefined
        ? `&usBoxOfficeFilterValue=${filterModel.usBoxOfficeFilterValue}`
        : '';

    const unweightedFilterParam =
      filterModel.unweightedFilter !== undefined
        ? `&unweightedFilter=${filterModel.unweightedFilter}`
        : '';
    const unweightedFilterValueParam =
      filterModel.unweightedFilterValue !== undefined
        ? `&unweightedFilterValue=${filterModel.unweightedFilterValue}`
        : '';

    const adOptimisationFilterParam =
      filterModel.adOptimisationFilter !== undefined
        ? `&adOptimisationFilter=${filterModel.adOptimisationFilter}`
        : '';
    const adOptimisationFilterValueParam =
      filterModel.adOptimisationFilterValue !== undefined
        ? `&adOptimisationFilterValue=${filterModel.adOptimisationFilterValue}`
        : '';
    const periodFilterParam =
      filterModel.periodFilter !== undefined
        ? `&periodFilter=${filterModel.periodFilter}`
        : '';

    const startDate =
      filterModel.periodFilterStartRange !== undefined
        ? new Date(filterModel.periodFilterStartRange)
        : '';
    const periodFilterStartRangeParam =
      filterModel.periodFilterStartRange !== undefined
        ? `&periodFilterStartRange=${DateService.toIsoDateFormat(
            startDate.setDate(startDate.getDate())
          )}`
        : '';

    const endDate =
      filterModel.periodFilterEndRange !== undefined
        ? new Date(filterModel.periodFilterEndRange)
        : '';
    const periodFilterEndRangeParam =
      filterModel.periodFilterEndRange !== undefined
        ? `&periodFilterEndRange=${DateService.toIsoDateFormat(
            endDate.setDate(endDate.getDate())
          )}`
        : '';
    const scoreSortParam =
      filterModel.userSort !== undefined
        ? `&generatedScoreSort=${filterModel.userSort}`
        : '';
    const sortByParam =
      filterModel.sortBy !== undefined ? `&sortBy=${filterModel.sortBy}` : '';
    const queryString = `${headerParam}&pageNumber=${tableParameters.pageNumber ||
      1}&pageSize=${
      tableParameters.pageSize
    }${idFilterParam}${iDFilterValueParam}${weightedFilterParam}${weightedFilterValueParam}${neatnessFilterParam}${neatnessFilterValueParam}${primetimeFilterParam}${primetimeFilterValueParam}${breakFilterParam}${breakFilterValueParam}${youthFilterParam}${youthFilterValueParam}${ukBoxOfficeFilterParam}${ukBoxOfficeFilterValueParam}${usBoxOfficeFilterParam}${usBoxOfficeFilterValueParam}${unweightedFilterParam}${unweightedFilterValueParam}${adOptimisationFilterParam}${adOptimisationFilterValueParam}${periodFilterParam}${periodFilterStartRangeParam}${periodFilterEndRangeParam}${scoreSortParam}${sortByParam}`;
    return queryString;
  };

  const getSchedules = () => {
    setIsLoading(true);
    const query = constructQueryString();
    ScheduleService.getSchedules(query)
      .then((response) => {
        setSchedules(response.data.scores);
        setPaginationParameters({
          totalCount: response.data.totalCount,
          pageSize: response.data.pageSize,
          currentPage: response.data.currentPage,
          totalPages: response.data.totalPages,
        });
        const responseResult = response.data;
        setProgress(responseResult.currentProgress);
        setIsGenerating(responseResult.currentProgress > 0);
        setDisabledGenerateTitles(!responseResult.generateEnabled);
        setDisabledGenerate(!responseResult.generateEnabled);
        setDisabledText(responseResult.generateText);
        setProgressText(responseResult.generateText);
        if (response.data.currentPage !== tableParameters.pageNumber) {
          setTableParameters({
            pageNumber: response.data.currentPage,
            pageSize: tableParameters.pageSize,
          });
        }
        setDisabledCreate(
          responseResult.generateText === 'No programme titles'
        );
      })
      .finally(() => setIsLoading(false));
  };

  const paginate = (pageNumber, size) => {
    setIsLoading(true);
    setTableParameters({ pageNumber, pageSize: size });

    setIsLoading(false);
  };

  const handleSetSortBy = (id) => {
    const newArr = sortBy;

    if (newArr[id].state === true) {
      newArr[id].state = false;
    } else {
      newArr[id].state = true;
    }

    setSortBy(newArr);
    setSortInfo({
      userSort: id,
      sortBy: newArr[id].state ? 1 : 2,
    });
  };

  const onClickedButtonFilter = (model) => {
    setFilterModel(model);
  };

  const onClickedButtonClearAllFilters = (startDate, endDate) => {
    setFilterModel({
      userSort: sortInfo.userSort,
      sortBy: sortInfo.sortBy,
      useCache: true,
    });
  };
  const clearFilterModel = (model) => {
    setFilterModel(model);
  };

  const callbackCheckPriorityList = () => {};

  const generateSchedule = () => {
    setDisabledGenerate(true);
    const param = {
      ChannelId: filter.channelId,
      Start: DateService.toIsoDateFormat(period.start),
      End: DateService.toIsoDateFormat(period.end),
    };
    ScheduleService.generateSchedules(param).then(() => {
      success('Generate process started');
      setDisabledGenerate(false);
      setModalShow(false);
      getSchedules();
    });
  };

  const callbackGenerateSchedule = () => {
    generateSchedule();
  };

  const callbackSelectSchedule = (data) => {
    authService.setSelectedSchedule({ data, filter, period });
    saveSchedulerSession(data);
  };

  const [renderTimes, setRenderTimes] = useState(0);
  const [scheduleSessionParameters, setScheduleSessionParameters] = useState();

  const constructScheduleSessionParameters = (parameters) => {
    const channelIdParam =
      parameters.channelId || parameters.channelID
        ? `?channelId=${
            parameters.channelId ? parameters.channelId : parameters.channelID
          }`
        : '';
    const fromParam = parameters.periodFrom
      ? `&periodFrom=${DateService.toIsoDateFormat(parameters.periodFrom)}`
      : '';
    const toParam = parameters.periodTo
      ? `&periodTo=${DateService.toIsoDateFormat(parameters.periodTo)}`
      : '';
    return `${channelIdParam}${fromParam}${toParam}`;
  };

  const handleGetScheduleSession = () => {
    setIsLoadingSession(true);
    ScheduleService.getScheduleSession().then((response) => {
      const currentDate = new Date();
      const channel = channelOptions.find(
        (o) => o.id === response.data.channelID
      );
      const month =
        response.data.monthID > 0
          ? ScheduleForms.months.find((o) => o.id === response.data.monthID)
          : ScheduleForms.months[currentDate.getMonth() - 1];
      const year = ScheduleForms.year().find(
        (o) => o.name.toString() === response.data.year
      );
      setScheduleSessionParameters(response.data);
      setFilter({
        ...filter,
        channelId: channel ? channel.id : null,
      });
      setDefaultSelected({
        channel: channel ? channel.name : 'Please select a channel',
      });

      setSelectedPeriod({
        start: new Date(response.data.periodFrom),
        end: new Date(response.data.periodTo),
      });
      setPeriod({
        start: new Date(response.data.periodFrom),
        end: new Date(response.data.periodTo),
      });
      if (channel) getTableColumns(channel.id);
      setIsLoadingSession(false);
    });
  };

  const handleSaveScheduleSession = () => {
    ScheduleService.saveScheduleSession(
      constructScheduleSessionParameters(scheduleSessionParameters)
    );
  };

  const onChangeFormSetScheduleSessionParameters = (data) => {
    setScheduleSessionParameters({
      ...scheduleSessionParameters,
      ...data,
    });
  };

  const saveSchedulerSession = (data) => {
    navigate(`${process.env.REACT_APP_SUBFOLDER}/scheduler`, {
      state: { data, filter, period },
    });
  };

  const callbackCreateSchedule = () => {
    const start = period.start;
    const end = period.end;
    const startString =
      ('0' + start.getDate()).slice(-2) +
      '/' +
      ('0' + (start.getMonth() + 1)).slice(-2) +
      '/' +
      start.getFullYear();
    const endString =
      ('0' + end.getDate()).slice(-2) +
      '/' +
      ('0' + (end.getMonth() + 1)).slice(-2) +
      '/' +
      end.getFullYear();
    const periodString = startString + ' - ' + endString;
    const data = { id: 0, period: periodString };
    authService.setSelectedSchedule({ data, filter, period });
    saveSchedulerSession(data);
  };

  const deleteSchedule = (selected) => {
    setDeleteID(selected.id);
    setDeleteModalShow(true);
  };

  const confirmDelete = () => {
    if (!isDeleting) {
      setIsDeleting(true);
      ScheduleService.deleteSchedule(deleteID)
        .then((response) => {
          success('Schedule successfully deleted');
          getSchedules();
        })
        .finally(() => {
          setIsDeleting(false);
          setDeleteModalShow(false);
        });
    }
  };

  useEffect(() => {
    if (state !== null) {
      const scheduleData = state.scheduledata;
      const { scheduleInfo } = state;
      setFilter(scheduleData);
      setDefaultSelected({
        channel: scheduleInfo.channelName,
        month: scheduleInfo.month,
        year: scheduleData.year,
      });
      getTableColumns(scheduleData.channelId);
      setPeriodFilterStartRange(state.period.start);
      setPeriodFilterEndRange(state.period.end);
    }
    getChannels();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (filter.channelId !== 0 && filter.channelId !== null && !isLoading) {
      getSchedules();
    } else setIsLoading(false);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [filter, filterModel, tableParameters, period]);

  useEffect(() => {
    if (channelOptions && !state) {
      handleGetScheduleSession();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [channelOptions]);

  useEffect(() => {
    if (scheduleSessionParameters) {
      if (renderTimes > 0) {
        handleSaveScheduleSession();
      }

      setRenderTimes(renderTimes + 1);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [scheduleSessionParameters]);

  useEffect(() => {
    setDisabledGenerate(disabledGenerateTitles);
    if (period.start < range.start) {
      setDisabledGenerate(true);
    }
    if (period.end > range.end) {
      setDisabledGenerate(true);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [period]);

  return (
    <div>
      <ScheduleBannerComponent
        componentName="Schedules"
        channels={channelOptions}
        callbackSelectChannelFunction={callBackSelectChannel}
        callbackSelectMonthFunction={callBackSelectMonth}
        callbackSelectYearFunction={callBackSelectYear}
        callbackGenerateSchedule={callbackGenerateSchedule}
        callbackCheckPriorityList={callbackCheckPriorityList}
        onChangeFormSetScheduleSessionParameters={
          onChangeFormSetScheduleSessionParameters
        }
        disabledGenerate={disabledGenerate}
        defaultSelected={defaultSelected}
        hiddenSelectChannels={hiddenSelectChannels}
        callbackCreateSchedule={callbackCreateSchedule}
        isLoading={isLoadingSession}
        callbackSelectPeriod={(value) => {
          setPeriod(value);
        }}
        disabledCreateSchedule={disabledCreate}
        setPeriodFilterStartRange={setPeriodFilterStartRange}
        setPeriodFilterEndRange={setPeriodFilterEndRange}
        selectedPeriod={selectedPeriod}
        setPeriod={(e) => setPeriod(e)}
      />
      {false && (
        <div
          style={{
            marginTop: '10px',
          }}
        >
          <ProgressBar
            variant="success"
            now={progress}
            label={`${progress}%`}
          />
          <div>{progressText}</div>
        </div>
      )}
      <div
        style={{
          marginTop: '10px',
        }}
      >
        {disabledText === 'No programme titles and segments' && (
          <div style={{ color: '#ff7878' }}>
            No programme titles and segments found.
          </div>
        )}
        {disabledText === 'No segments' && (
          <div style={{ color: '#ff7878' }}>
            No segments found for the available programme titles under the
            selected period of the selected channel. To generate schedules,
            please go to <strong>Prioritisation</strong>, select the channel
            with the same period, then click <strong>Get Segments</strong>.
          </div>
        )}
        {disabledText === 'No programme titles' && (
          <div style={{ color: '#ff7878' }}>
            No programme titles found for the selected channel and period.
          </div>
        )}
      </div>
      <hr style={{ background: '#c8c8c8' }} />

      <div className="d-flex">
        <div style={{ width: '100%' }}>
          <TableComponent
            component={
              <ScheduleDataComponent
                schedules={schedules}
                callbackSelectSchedule={callbackSelectSchedule}
                isLoading={isLoading}
                scoringColumns={scoringColumns}
                disabledGenerate={disabledGenerate}
                deleteSchedule={deleteSchedule}
              />
            }
            paginationParameters={paginationParameters}
            paginate={paginate}
            stylesTable={Styles.divTableHeight}
            hasExport={false}
            tableHeader={
              <ScheduleTableHeaderComponent
                filterModel={filterModel}
                sortBy={sortBy}
                handleSetSortBy={handleSetSortBy}
                onClickedButtonFilter={onClickedButtonFilter}
                onClickedButtonClearAllFilters={onClickedButtonClearAllFilters}
                clearFilterModel={clearFilterModel}
                scoringColumns={scoringColumns}
              />
            }
            isTableBodyLoading={isLoading}
          />
        </div>
      </div>

      <Modal
        size="md"
        show={modalShow}
        onHide={() => setModalShow(false)}
        aria-labelledby="example-modal-sizes-title-lg"
      >
        <Modal.Body style={{ padding: '35px' }}>
          <div style={{ textAlign: 'center' }}>
            There are already generated schedules for this channel under the
            selected period.{' '}
          </div>
          <div style={{ textAlign: 'center' }}>
            Generating new schedules will delete all existing schedules of this
            channel under the selected period.
          </div>
          <div style={{ textAlign: 'center', marginTop: '20px' }}>
            <b> Are you sure you want to generate again?</b>
          </div>
          <div
            style={{
              textAlign: 'center',
              paddingTop: '30px',
            }}
          >
            <button
              className={Styles.cancelButton}
              onClick={() => setModalShow(false)}
              style={{ marginLeft: '0' }}
              type="button"
            >
              Cancel
            </button>
            <button
              className={Styles.saveButton}
              onClick={() => generateSchedule()}
              type="button"
            >
              Generate
            </button>
          </div>
        </Modal.Body>
      </Modal>

      <YesNoModalComponent
        closeModal={() => setDeleteModalShow(false)}
        confirmModal={confirmDelete}
        yesButtonText={'Delete'}
        showModal={deleteModalShow}
        header={'Are you sure you want to delete this schedule?'}
        description={
          'This action cannot be undone and all data associated with this schedule will be permanently removed.'
        }
      />
    </div>
  );
}

export default ScheduleComponent;
