import React from 'react';
import PropTypes from 'prop-types';
import Styles from './Schedule.module.css';
import viewIcon from '../../assets/icons/Schedule-List.svg';
import { deleteIcon } from '../../assets/index';

function ScheduleDataComponent({
  schedules,
  callbackSelectSchedule,
  isLoading,
  scoringColumns,
  deleteSchedule,
}) {
  const selectSchedule = (data) => {
    //  if (!disabledGenerate) {
    callbackSelectSchedule(data);
    //  }
  };
  return (
    <>
      {schedules.length === 0 && (
        <tr className={`${Styles.loadingHeight}`}>
          <td
            colSpan="7"
            style={{
              fontSize: '50px',
              color: '#13ad83',
              textAlign: 'center',
              backgroundColor: 'transparent',
            }}
          >
            {isLoading ? (
              // <div className="spinner-border" role="status" />
              <p style={{ textAlign: 'center' }}>
                <i
                  className={`${Styles.faCircleNotchSpin} fas fa-circle-notch fa-spin`}
                />
              </p>
            ) : (
              <>No Schedules Found</>
            )}
          </td>
        </tr>
      )}
      {schedules.length > 0 && (
        <>
          {schedules.map((data) => (
            <tr
              key={data.id}
              className={`${'table-body-row'}`}
              style={{ cursor: 'default' }}
            >
              <td
                className={`${Styles.tdID} table-body-column`}
                data-toggle="tooltip"
                data-placement="top"
                title={data.id}
              >
                {data.id}
              </td>
              <td
                className={`${Styles.td} table-body-column`}
                data-toggle="tooltip"
                data-placement="top"
                title={data.period}
              >
                {data.period}
              </td>
              <td
                className={`table-body-column ${Styles.tableColumnNumber}`}
                data-toggle="tooltip"
                data-placement="top"
                title={data.weighted}
              >
                {data.weighted}
              </td>
              {scoringColumns.neatness && (
                <td
                  className={`table-body-column ${Styles.tableColumnNumber}`}
                  data-toggle="tooltip"
                  data-placement="top"
                  title={data.neatness}
                >
                  {data.neatness}
                </td>
              )}
              {scoringColumns.strengthofPrimetime && (
                <td
                  className={`table-body-column ${Styles.tableColumnNumber}`}
                  data-toggle="tooltip"
                  data-placement="top"
                  title={data.primetime}
                >
                  {data.primetime}
                </td>
              )}
              {scoringColumns.averageBreakTime && (
                <td
                  className={`table-body-column ${Styles.tableColumnNumber}`}
                  data-toggle="tooltip"
                  data-placement="top"
                  title={data.break}
                >
                  {data.break}
                </td>
              )}
              {scoringColumns.youthofAsset && (
                <td
                  className={`table-body-column ${Styles.tableColumnNumber}`}
                  data-toggle="tooltip"
                  data-placement="top"
                  title={data.youth}
                >
                  {data.youth}
                </td>
              )}
              {scoringColumns.ukBoxOffice && (
                <td
                  className={`table-body-column ${Styles.tableColumnNumber}`}
                  data-toggle="tooltip"
                  data-placement="top"
                  title={data.ukBoxOffice}
                >
                  {data.ukBoxOffice}
                </td>
              )}
              {scoringColumns.usBoxOffice && (
                <td
                  className={`table-body-column ${Styles.tableColumnNumber}`}
                  data-toggle="tooltip"
                  data-placement="top"
                  title={data.usBoxOffice}
                >
                  {data.usBoxOffice}
                </td>
              )}
              {scoringColumns.adOptimisation && (
                <td
                  className={`table-body-column ${Styles.tableColumnNumber}`}
                  data-toggle="tooltip"
                  data-placement="top"
                  title={data.adOptimisation}
                >
                  {data.adOptimisation}
                </td>
              )}
              <td
                className={`table-body-column ${Styles.tableColumnNumber}`}
                data-toggle="tooltip"
                data-placement="top"
                title={data.unweighted}
              >
                {data.unweighted}
              </td>
              <td className="table-body-column" style={{ textIndent: '60px' }}>
                <img
                  name={data.id}
                  style={{
                    cursor: 'pointer',
                    marginLeft: '15px',
                    marginRight: '15px',
                    float: 'right',
                  }}
                  src={deleteIcon}
                  alt="ViewIcon"
                  onClick={() => deleteSchedule(data)}
                  onKeyDown={() => deleteSchedule(data)}
                  role="presentation"
                  data-toggle="tooltip"
                  data-placement="top"
                  title="Delete schedule"
                />
                <img
                  name={data.id}
                  style={{
                    cursor: 'pointer',
                    marginLeft: '15px',
                    float: 'right',
                  }}
                  src={viewIcon}
                  alt="ViewIcon"
                  onClick={() => selectSchedule(data)}
                  onKeyDown={() => selectSchedule(data)}
                  role="presentation"
                  data-toggle="tooltip"
                  data-placement="top"
                  title="View schedule"
                />
              </td>
            </tr>
          ))}
        </>
      )}
    </>
  );
}
ScheduleDataComponent.propTypes = {
  schedules: PropTypes.arrayOf(PropTypes.shape).isRequired,
  callbackSelectSchedule: PropTypes.func.isRequired,
  isLoading: PropTypes.bool,
  scoringColumns: PropTypes.shape.isRequired,
};
ScheduleDataComponent.defaultProps = {
  isLoading: true,
};
export default ScheduleDataComponent;
