import React, { useState, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import styles from './ScheduleTableHeader.module.css';
import filterIcon from '../../../../assets/icons/Filter.png';
import filteredIcon from '../../../../assets/icons/Filtered.png';
import arrowUpIcon from '../../../../assets/icons/Arrow-Up.png';
import clearFilterIcon from '../../../../assets/icons/Clear-Filter.svg';

import NumberFilterComponent from './filters/number-filter/number.filter.component';
import DateFilterComponent from './filters/date-filter/date.filter.component';

function ScheduleTableHeaderComponent(props) {
  const {
    sortBy,
    filterModel,
    onClickedButtonFilter,
    handleSetSortBy,
    onClickedButtonClearAllFilters,
    onClickedButtonClearFilter,
    scoringColumns,
  } = props;
  const [showIDFilter, setShowIDFilter] = useState(false);
  const [showWeightedFilter, setShowWeightedFilter] = useState(false);
  const [showNeatnessFilter, setShowNeatnessFilter] = useState(false);
  const [showPrimetimeFilter, setShowPrimetimeFilter] = useState(false);
  const [showBreakFilter, setShowBreakFilter] = useState(false);
  const [showYouthFilter, setShowYouthFilter] = useState(false);
  const [showUkBoxOfficeFilter, setShowUkBoxOfficeFilter] = useState(false);
  const [showUsBoxOfficeFilter, setShowUsBoxOfficeFilter] = useState(false);
  const [showUnweightedFilter, setShowUnweightedFilter] = useState(false);
  const [showClearFilterIcon, setShowClearAllFilterIcon] = useState(false);
  const [showAdOptimisationFilter, setShowAdOptimisationFilter] = useState(
    false
  );
  const [showPeriodFilter, setShowPeriodFilter] = useState(false);

  const iDFilterRef = useRef();
  const weightedFilterRef = useRef();
  const neatnessFilterRef = useRef();
  const primetimeFilterRef = useRef();
  const breakFilterRef = useRef();
  const youthFilterRef = useRef();
  const ukBoxOfficeFilterRef = useRef();
  const usBoxOfficeFilterRef = useRef();
  const unweightedFilterRef = useRef();
  const adFilterRef = useRef();
  const periodFilterRef = useRef();

  const toggleDropdown = (header) => {
    if (header === 'ID') {
      setShowIDFilter(!showIDFilter);
    } else {
      setShowIDFilter(false);
    }

    if (header === 'Weighted') {
      setShowWeightedFilter(!showWeightedFilter);
    } else {
      setShowWeightedFilter(false);
    }

    if (header === 'Neatness') {
      setShowNeatnessFilter(!showNeatnessFilter);
    } else {
      setShowNeatnessFilter(false);
    }

    if (header === 'Primetime') {
      setShowPrimetimeFilter(!showPrimetimeFilter);
    } else {
      setShowPrimetimeFilter(false);
    }

    if (header === 'Break') {
      setShowBreakFilter(!showBreakFilter);
    } else {
      setShowBreakFilter(false);
    }

    if (header === 'Youth') {
      setShowYouthFilter(!showYouthFilter);
    } else {
      setShowYouthFilter(false);
    }

    if (header === 'UkBoxOffice') {
      setShowUkBoxOfficeFilter(!showUkBoxOfficeFilter);
    } else {
      setShowUkBoxOfficeFilter(false);
    }

    if (header === 'UsBoxOffice') {
      setShowUsBoxOfficeFilter(!showUsBoxOfficeFilter);
    } else {
      setShowUsBoxOfficeFilter(false);
    }

    if (header === 'Unweighted') {
      setShowUnweightedFilter(!showUnweightedFilter);
    } else {
      setShowUnweightedFilter(false);
    }

    if (header === 'AdOptimisation') {
      setShowAdOptimisationFilter(!showAdOptimisationFilter);
    } else {
      setShowAdOptimisationFilter(false);
    }

    if (header === 'Period') {
      setShowPeriodFilter(!showPeriodFilter);
    } else {
      setShowPeriodFilter(false);
    }
  };

  const backdropClicked = (header) => {
    if (header === 'ID') {
      setShowIDFilter(false);
    }

    if (header === 'Weighted') {
      setShowWeightedFilter(false);
    }

    if (header === 'Neatness') {
      setShowNeatnessFilter(false);
    }

    if (header === 'Primetime') {
      setShowPrimetimeFilter(false);
    }

    if (header === 'Break') {
      setShowBreakFilter(false);
    }

    if (header === 'Youth') {
      setShowYouthFilter(false);
    }

    if (header === 'UkBoxOffice') {
      setShowUkBoxOfficeFilter(false);
    }

    if (header === 'UsBoxOffice') {
      setShowUsBoxOfficeFilter(false);
    }

    if (header === 'Unweighted') {
      setShowUnweightedFilter(false);
    }

    if (header === 'AdOptimisation') {
      setShowAdOptimisationFilter(false);
    }
    if (header === 'Period') {
      setShowPeriodFilter(false);
    }
  };

  const onClickedButtonFilterHeader = (model) => {
    onClickedButtonFilter(model);
  };

  const handleSort = (id) => {
    const model = JSON.parse(JSON.stringify(filterModel));
    model.userSort = id;
    model.sortBy = sortBy[id].state ? 2 : 1;
    onClickedButtonFilterHeader(model);
    handleSetSortBy(id);
  };

  const onClickedButtonClearAllFiltersHeader = () => {
    if (iDFilterRef.current !== null && iDFilterRef.current !== undefined)
      iDFilterRef.current.clearFilterModel();
    if (
      weightedFilterRef.current !== null &&
      weightedFilterRef.current !== undefined
    )
      weightedFilterRef.current.clearFilterModel();
    if (
      neatnessFilterRef.current !== null &&
      neatnessFilterRef.current !== undefined
    )
      neatnessFilterRef.current.clearFilterModel();
    if (
      primetimeFilterRef.current !== null &&
      primetimeFilterRef.current !== undefined
    )
      primetimeFilterRef.current.clearFilterModel();
    if (breakFilterRef.current !== null && breakFilterRef.current !== undefined)
      breakFilterRef.current.clearFilterModel();
    if (youthFilterRef.current !== null && youthFilterRef.current !== undefined)
      youthFilterRef.current.clearFilterModel();
    if (
      ukBoxOfficeFilterRef.current !== null &&
      ukBoxOfficeFilterRef.current !== undefined
    )
      ukBoxOfficeFilterRef.current.clearFilterModel();
    if (
      usBoxOfficeFilterRef.current !== null &&
      usBoxOfficeFilterRef.current !== undefined
    )
      usBoxOfficeFilterRef.current.clearFilterModel();
    if (
      unweightedFilterRef.current !== null &&
      unweightedFilterRef.current !== undefined
    )
      unweightedFilterRef.current.clearFilterModel();
    if (adFilterRef.current !== null && adFilterRef.current !== undefined) {
      adFilterRef.current.clearFilterModel();
    }

    if (
      periodFilterRef.current !== null &&
      periodFilterRef.current !== undefined
    ) {
      periodFilterRef.current.clearFilterModel();
    }
    onClickedButtonClearAllFilters(
      filterModel.periodFilterStartRange,
      filterModel.periodFilterEndRange
    );
  };

  const handleClearFilterIcon = () => {
    setShowClearAllFilterIcon(
      !!(
        filterModel.iDFilter ||
        filterModel.iDFilterValue ||
        filterModel.weightedFilter ||
        filterModel.weightedFilterValue ||
        filterModel.neatnessFilter ||
        filterModel.neatnessFilterValue ||
        filterModel.primetimeFilter ||
        filterModel.primetimeFilterValue ||
        filterModel.breakFilter ||
        filterModel.breakFilterValue ||
        filterModel.youthFilter ||
        filterModel.youthFilterValue ||
        filterModel.ukBoxOfficeFilter ||
        filterModel.ukBoxOfficeFilterValue ||
        filterModel.usBoxOfficeFilter ||
        filterModel.usBoxOfficeFilterValue ||
        filterModel.unweightedFilter ||
        filterModel.unweightedFilterValue ||
        filterModel.adOptimisationFilter ||
        filterModel.adOptimisationFilterValue ||
        filterModel.periodFilter ||
        filterModel.periodFilterStartRange ||
        filterModel.periodFilterEndRange
      )
    );
  };

  useEffect(() => {
    handleClearFilterIcon();
    // eslint-disable-next-line
  }, [filterModel]);

  return (
    <tr>
      <td className={`${styles.columns} ${styles.columnID}`}>
        <span
          onClick={() => handleSort(1)}
          onKeyDown={() => handleSort(1)}
          role="presentation"
        >
          ID
        </span>{' '}
        <img
          className={`${styles.filterIcons} mx-2`}
          src={
            filterModel.iDFilter && filterModel.iDFilterValue
              ? filteredIcon
              : filterIcon
          }
          alt="FilterIcon1"
          onClick={() => toggleDropdown('ID')}
          onKeyDown={() => toggleDropdown('ID')}
          role="presentation"
        />
        <div
          className={styles.dropdownFirstname}
          style={{ display: showIDFilter ? null : 'none' }}
        >
          <NumberFilterComponent
            ref={iDFilterRef}
            component="ID"
            filterModel={filterModel}
            onClickedButtonFilter={onClickedButtonFilterHeader}
            onClickedButtonClearFilter={() => onClickedButtonClearFilter('ID')}
            toggleDropdown={toggleDropdown}
            backdropClicked={backdropClicked}
          />
        </div>
        <img
          src={arrowUpIcon}
          alt="ArrowUpIcon1"
          style={{
            display: filterModel.userSort === 1 ? null : 'none',
            transform: sortBy[1].state ? 'rotate(0deg)' : 'rotate(180deg)',
          }}
        />
      </td>
      <td className={`${styles.columns} ${styles.columns2}`}>
        <div style={{ display: 'flex' }}>
          <div
            onClick={() => handleSort(11)}
            onKeyDown={() => handleSort(11)}
            role="presentation"
            className={styles.label}
            title="Period"
          >
            Period
          </div>{' '}
          <div className={styles.icon}>
            <img
              className={`${styles.filterIcons} mx-2`}
              src={
                filterModel.periodFilter &&
                filterModel.periodFilterStartRange &&
                filterModel.periodFilterEndRange
                  ? filteredIcon
                  : filterIcon
              }
              alt="FilterIcon2"
              onClick={() => toggleDropdown('Period')}
              onKeyDown={() => toggleDropdown('Period')}
              role="presentation"
            />
          </div>
          <div
            className={styles.dropdownValidityDate}
            style={{ display: showPeriodFilter ? null : 'none' }}
          >
            <DateFilterComponent
              ref={periodFilterRef}
              component="Period"
              filterModel={filterModel}
              onClickedButtonFilter={onClickedButtonFilterHeader}
              onClickedButtonClearFilter={() =>
                onClickedButtonClearFilter('Period')
              }
              toggleDropdown={toggleDropdown}
              backdropClicked={backdropClicked}
            />
          </div>
          <div className={styles.icon}>
            <img
              src={arrowUpIcon}
              alt="ArrowUpIcon2"
              style={{
                display: filterModel.userSort === 11 ? null : 'none',
                transform: sortBy[11].state ? 'rotate(0deg)' : 'rotate(180deg)',
              }}
            />
          </div>
        </div>
      </td>

      <td className={styles.columns}>
        <div className={styles.numberDiv}>
          <div
            onClick={() => handleSort(2)}
            onKeyDown={() => handleSort(2)}
            role="presentation"
            className={styles.label}
            title="Weighted"
          >
            Weighted
          </div>{' '}
          <div className={styles.icon}>
            <img
              className={`${styles.filterIcons} mx-2`}
              src={
                filterModel.weightedFilter && filterModel.weightedFilterValue
                  ? filteredIcon
                  : filterIcon
              }
              alt="FilterIcon2"
              onClick={() => toggleDropdown('Weighted')}
              onKeyDown={() => toggleDropdown('Weighted')}
              role="presentation"
            />
          </div>
          <div
            className={styles.dropdownLastname}
            style={{ display: showWeightedFilter ? null : 'none' }}
          >
            <NumberFilterComponent
              ref={weightedFilterRef}
              component="Weighted"
              filterModel={filterModel}
              onClickedButtonFilter={onClickedButtonFilterHeader}
              onClickedButtonClearFilter={() =>
                onClickedButtonClearFilter('Weighted')
              }
              toggleDropdown={toggleDropdown}
              backdropClicked={backdropClicked}
            />
          </div>
          <div
            className={`${styles.icon} ${
              filterModel.userSort === 2 ? '' : styles.noIcon
            }`}
          >
            <img
              src={arrowUpIcon}
              alt="ArrowUpIcon2"
              style={{
                display: filterModel.userSort === 2 ? null : 'none',
                transform: sortBy[2].state ? 'rotate(0deg)' : 'rotate(180deg)',
              }}
            />
          </div>
        </div>
      </td>
      {scoringColumns.neatness && (
        <td className={styles.columns}>
          <div className={styles.numberDiv}>
            <div
              onClick={() => handleSort(3)}
              onKeyDown={() => handleSort(3)}
              role="presentation"
              className={styles.label}
              title="Neatness"
            >
              Neatness
            </div>{' '}
            <div
              className={`${styles.icon} ${
                filterModel.neatnessFilter && filterModel.neatnessFilterValue
                  ? styles.noIcon
                  : ''
              }`}
            >
              <img
                className={`${styles.filterIcons} mx-2`}
                src={
                  filterModel.neatnessFilter && filterModel.neatnessFilterValue
                    ? filteredIcon
                    : filterIcon
                }
                alt="FilterIcon3"
                onClick={() => toggleDropdown('Neatness')}
                onKeyDown={() => toggleDropdown('Neatness')}
                role="presentation"
              />
            </div>
            <div
              className={`${styles.icon} ${
                filterModel.userSort === 3 ? '' : styles.noIcon
              }`}
            >
              <img
                src={arrowUpIcon}
                alt="ArrowUpIcon3"
                style={{
                  display: filterModel.userSort === 3 ? null : 'none',
                  transform: sortBy[3].state
                    ? 'rotate(0deg)'
                    : 'rotate(180deg)',
                }}
              />
            </div>
            <div
              className={styles.dropdownEmail}
              style={{ display: showNeatnessFilter ? null : 'none' }}
            >
              <NumberFilterComponent
                ref={neatnessFilterRef}
                component="Neatness"
                filterModel={filterModel}
                onClickedButtonFilter={onClickedButtonFilterHeader}
                onClickedButtonClearFilter={() =>
                  onClickedButtonClearFilter('Neatness')
                }
                toggleDropdown={toggleDropdown}
                backdropClicked={backdropClicked}
              />
            </div>
          </div>
        </td>
      )}
      {scoringColumns.strengthofPrimetime && (
        <td className={styles.columns}>
          <div className={styles.numberDiv}>
            <div
              onClick={() => handleSort(4)}
              onKeyDown={() => handleSort(4)}
              role="presentation"
              className={styles.label}
              title="Primetime"
            >
              Primetime
            </div>{' '}
            <div className={styles.icon}>
              <img
                className={`${styles.filterIcons} mx-2`}
                src={
                  filterModel.primetimeFilter &&
                  filterModel.primetimeFilterValue
                    ? filteredIcon
                    : filterIcon
                }
                alt="FilterIcon3"
                onClick={() => toggleDropdown('Primetime')}
                onKeyDown={() => toggleDropdown('Primetime')}
                role="presentation"
              />
            </div>
            <div
              className={`${styles.icon} ${
                filterModel.userSort === 4 ? '' : styles.noIcon
              }`}
            >
              <img
                src={arrowUpIcon}
                alt="ArrowUpIcon3"
                style={{
                  display: filterModel.userSort === 4 ? null : 'none',
                  transform: sortBy[4].state
                    ? 'rotate(0deg)'
                    : 'rotate(180deg)',
                }}
              />
            </div>
            <div
              className={styles.dropdownEmail}
              style={{ display: showPrimetimeFilter ? null : 'none' }}
            >
              <NumberFilterComponent
                ref={primetimeFilterRef}
                component="Primetime"
                filterModel={filterModel}
                onClickedButtonFilter={onClickedButtonFilterHeader}
                onClickedButtonClearFilter={() =>
                  onClickedButtonClearFilter('Primetime')
                }
                toggleDropdown={toggleDropdown}
                backdropClicked={backdropClicked}
              />
            </div>
          </div>
        </td>
      )}
      {scoringColumns.averageBreakTime && (
        <td className={styles.columns}>
          <div className={styles.numberDiv}>
            <div
              onClick={() => handleSort(5)}
              onKeyDown={() => handleSort(5)}
              role="presentation"
              className={styles.label}
              title="Break"
            >
              Break
            </div>{' '}
            <div className={styles.icon}>
              <img
                className={`${styles.filterIcons} mx-2`}
                src={
                  filterModel.breakFilter && filterModel.breakFilterValue
                    ? filteredIcon
                    : filterIcon
                }
                alt="FilterIcon3"
                onClick={() => toggleDropdown('Break')}
                onKeyDown={() => toggleDropdown('Break')}
                role="presentation"
              />
            </div>
            <div
              className={`${styles.icon} ${
                filterModel.userSort === 5 ? '' : styles.noIcon
              }`}
            >
              <img
                src={arrowUpIcon}
                alt="ArrowUpIcon3"
                style={{
                  display: filterModel.userSort === 5 ? null : 'none',
                  transform: sortBy[5].state
                    ? 'rotate(0deg)'
                    : 'rotate(180deg)',
                }}
              />
            </div>
            <div
              className={styles.dropdownEmail}
              style={{ display: showBreakFilter ? null : 'none' }}
            >
              <NumberFilterComponent
                ref={breakFilterRef}
                component="Break"
                filterModel={filterModel}
                onClickedButtonFilter={onClickedButtonFilterHeader}
                onClickedButtonClearFilter={() =>
                  onClickedButtonClearFilter('Break')
                }
                toggleDropdown={toggleDropdown}
                backdropClicked={backdropClicked}
              />
            </div>
          </div>
        </td>
      )}
      {scoringColumns.youthofAsset && (
        <td className={styles.columns}>
          <div className={styles.numberDiv}>
            <div
              onClick={() => handleSort(6)}
              onKeyDown={() => handleSort(6)}
              role="presentation"
              className={styles.label}
              title="Youth"
            >
              Youth
            </div>{' '}
            <div className={styles.icon}>
              <img
                className={`${styles.filterIcons} mx-2`}
                src={
                  filterModel.youthFilter && filterModel.youthFilterValue
                    ? filteredIcon
                    : filterIcon
                }
                alt="FilterIcon3"
                onClick={() => toggleDropdown('Youth')}
                onKeyDown={() => toggleDropdown('Youth')}
                role="presentation"
              />
            </div>
            <div
              className={`${styles.icon} ${
                filterModel.userSort === 6 ? '' : styles.noIcon
              }`}
            >
              <img
                src={arrowUpIcon}
                alt="ArrowUpIcon3"
                style={{
                  display: filterModel.userSort === 6 ? null : 'none',
                  transform: sortBy[6].state
                    ? 'rotate(0deg)'
                    : 'rotate(180deg)',
                }}
              />
            </div>
            <div
              className={styles.dropdownEmail}
              style={{ display: showYouthFilter ? null : 'none' }}
            >
              <NumberFilterComponent
                ref={youthFilterRef}
                component="Youth"
                filterModel={filterModel}
                onClickedButtonFilter={onClickedButtonFilterHeader}
                onClickedButtonClearFilter={() =>
                  onClickedButtonClearFilter('Youth')
                }
                toggleDropdown={toggleDropdown}
                backdropClicked={backdropClicked}
              />
            </div>
          </div>
        </td>
      )}
      {scoringColumns.ukBoxOffice && (
        <td className={styles.columns}>
          <div className={styles.numberDiv}>
            <div
              onClick={() => handleSort(7)}
              onKeyDown={() => handleSort(7)}
              role="presentation"
              className={styles.label}
              title="UK Box Office"
            >
              UK Box Office
            </div>
            <div className={styles.icon}>
              <img
                className={`${styles.filterIcons} mx-2`}
                src={
                  filterModel.ukBoxOfficeFilter &&
                  filterModel.ukBoxOfficeFilterValue
                    ? filteredIcon
                    : filterIcon
                }
                alt="FilterIcon3"
                onClick={() => toggleDropdown('UkBoxOffice')}
                onKeyDown={() => toggleDropdown('UkBoxOffice')}
                role="presentation"
              />
            </div>
            <div
              className={`${styles.icon} ${
                filterModel.userSort === 7 ? '' : styles.noIcon
              }`}
            >
              <img
                src={arrowUpIcon}
                alt="ArrowUpIcon3"
                style={{
                  display: filterModel.userSort === 7 ? null : 'none',
                  transform: sortBy[7].state
                    ? 'rotate(0deg)'
                    : 'rotate(180deg)',
                }}
              />
            </div>
            <div
              className={styles.dropdownEmail}
              style={{ display: showUkBoxOfficeFilter ? null : 'none' }}
            >
              <NumberFilterComponent
                ref={ukBoxOfficeFilterRef}
                component="UkBoxOffice"
                filterModel={filterModel}
                onClickedButtonFilter={onClickedButtonFilterHeader}
                onClickedButtonClearFilter={() =>
                  onClickedButtonClearFilter('UkBoxOffice')
                }
                toggleDropdown={toggleDropdown}
                backdropClicked={backdropClicked}
              />
            </div>
          </div>
        </td>
      )}
      {scoringColumns.usBoxOffice && (
        <td className={styles.columns}>
          <div className={styles.numberDiv}>
            <div
              onClick={() => handleSort(8)}
              onKeyDown={() => handleSort(8)}
              role="presentation"
              className={styles.label}
              title="US Box Office"
            >
              US Box Office
            </div>
            <div className={styles.icon}>
              <img
                className={`${styles.filterIcons} mx-2`}
                src={
                  filterModel.usBoxOfficeFilter &&
                  filterModel.usBoxOfficeFilterValue
                    ? filteredIcon
                    : filterIcon
                }
                alt="FilterIcon3"
                onClick={() => toggleDropdown('UsBoxOffice')}
                onKeyDown={() => toggleDropdown('UsBoxOffice')}
                role="presentation"
              />
            </div>
            <div
              className={`${styles.icon} ${
                filterModel.userSort === 8 ? '' : styles.noIcon
              }`}
            >
              <img
                src={arrowUpIcon}
                alt="ArrowUpIcon3"
                style={{
                  display: filterModel.userSort === 8 ? null : 'none',
                  transform: sortBy[8].state
                    ? 'rotate(0deg)'
                    : 'rotate(180deg)',
                }}
              />
            </div>

            <div
              className={styles.dropdownEmail}
              style={{ display: showUsBoxOfficeFilter ? null : 'none' }}
            >
              <NumberFilterComponent
                ref={usBoxOfficeFilterRef}
                component="UsBoxOffice"
                filterModel={filterModel}
                onClickedButtonFilter={onClickedButtonFilterHeader}
                onClickedButtonClearFilter={() =>
                  onClickedButtonClearFilter('UsBoxOffice')
                }
                toggleDropdown={toggleDropdown}
                backdropClicked={backdropClicked}
              />
            </div>
          </div>
        </td>
      )}

      {scoringColumns.adOptimisation && (
        <td className={styles.columns}>
          <div className={styles.numberDiv}>
            <div
              onClick={() => handleSort(10)}
              onKeyDown={() => handleSort(10)}
              role="presentation"
              className={styles.label}
              title="AD Optimisation"
            >
              AD Optimisation
            </div>
            <div className={styles.icon}>
              <img
                className={`${styles.filterIcons} mx-2`}
                src={
                  filterModel.adOptimisationFilter &&
                  filterModel.adOptimisationFilterValue
                    ? filteredIcon
                    : filterIcon
                }
                alt="FilterIcon3"
                onClick={() => toggleDropdown('AdOptimisation')}
                onKeyDown={() => toggleDropdown('AdOptimisation')}
                role="presentation"
              />
            </div>
            <div
              className={`${styles.icon} ${
                filterModel.userSort === 10 ? '' : styles.noIcon
              }`}
            >
              <img
                src={arrowUpIcon}
                alt="ArrowUpIcon3"
                style={{
                  display: filterModel.userSort === 10 ? null : 'none',
                  transform: sortBy[10].state
                    ? 'rotate(0deg)'
                    : 'rotate(180deg)',
                }}
              />
            </div>
            <div
              className={styles.dropdownEmail}
              style={{ display: showAdOptimisationFilter ? null : 'none' }}
            >
              <NumberFilterComponent
                ref={adFilterRef}
                component="AdOptimisation"
                filterModel={filterModel}
                onClickedButtonFilter={onClickedButtonFilterHeader}
                onClickedButtonClearFilter={() =>
                  onClickedButtonClearFilter('AdOptimisation')
                }
                toggleDropdown={toggleDropdown}
                backdropClicked={backdropClicked}
              />
            </div>
          </div>
        </td>
      )}

      <td className={styles.columns}>
        <div className={styles.numberDiv}>
          <div
            onClick={() => handleSort(9)}
            onKeyDown={() => handleSort(9)}
            role="presentation"
            className={styles.label}
            title="Unweighted"
          >
            Unweighted
          </div>
          <div
            className={`${styles.icon} ${
              filterModel.userSort === 9 ? '' : styles.noIcon
            }`}
          >
            <img
              className={`${styles.filterIcons} mx-2`}
              src={
                filterModel.unweightedFilter &&
                filterModel.unweightedFilterValue
                  ? filteredIcon
                  : filterIcon
              }
              alt="FilterIcon3"
              onClick={() => toggleDropdown('Unweighted')}
              onKeyDown={() => toggleDropdown('Unweighted')}
              role="presentation"
            />
          </div>
          <div className={styles.icon}>
            <img
              src={arrowUpIcon}
              alt="ArrowUpIcon3"
              style={{
                display: filterModel.userSort === 9 ? null : 'none',
                transform: sortBy[9].state ? 'rotate(0deg)' : 'rotate(180deg)',
              }}
            />
          </div>

          <div
            className={styles.dropdownEmail}
            style={{ display: showUnweightedFilter ? null : 'none' }}
          >
            <NumberFilterComponent
              ref={unweightedFilterRef}
              component="Unweighted"
              filterModel={filterModel}
              onClickedButtonFilter={onClickedButtonFilterHeader}
              onClickedButtonClearFilter={() =>
                onClickedButtonClearFilter('Unweighted')
              }
              toggleDropdown={toggleDropdown}
              backdropClicked={backdropClicked}
            />
          </div>
        </div>
      </td>

      <td className={styles.columns}>
        <div className={styles.numberDiv}>
          <div
            style={{ cursor: 'default' }}
            className={styles.label}
            title="Schedule"
          >
            Schedule
          </div>
          <div className={styles.icon}>
            <img
              className={`${styles.clearFilterIcons}`}
              style={{ display: showClearFilterIcon ? null : 'none' }}
              src={clearFilterIcon}
              alt="ClearFilterIcon"
              onClick={onClickedButtonClearAllFiltersHeader}
              onKeyDown={onClickedButtonClearAllFiltersHeader}
              role="presentation"
            />
          </div>
        </div>
      </td>
    </tr>
  );
}
ScheduleTableHeaderComponent.propTypes = {
  sortBy: PropTypes.arrayOf(PropTypes.shape({ state: PropTypes.bool }))
    .isRequired,
  filterModel: PropTypes.shape.isRequired,
  onClickedButtonFilter: PropTypes.func.isRequired,
  handleSetSortBy: PropTypes.func.isRequired,
  onClickedButtonClearAllFilters: PropTypes.func.isRequired,
  onClickedButtonClearFilter: PropTypes.func.isRequired,
  scoringColumns: PropTypes.shape.isRequired,
};
export default ScheduleTableHeaderComponent;
