import React from 'react';
import PropTypes from 'prop-types';
import { Modal } from 'react-bootstrap';
import styles from './YesNoModal.module.css';

export default function YesNoModalComponent({
  closeModal,
  confirmModal,
  yesButtonText,
  noButtonText,
  showModal,
  header,
  description,
}) {
  return (
    <Modal show={showModal} centered size="md">
      <div className={styles.body} style={{ textAlign: 'center' }}>
        <div>
          <p className={styles.confirmationText}>{header}</p>

          <p className={styles.description}>{description}</p>
        </div>

        <div className={`${styles.marginTop30}`}>
          <button
            className={`${styles.cancelButton} ${styles.marginRight10}`}
            onClick={closeModal}
            type="button"
          >
            {noButtonText}
          </button>
          <button
            className={styles.saveButton}
            onClick={confirmModal}
            type="button"
          >
            {yesButtonText}
          </button>
        </div>
      </div>
    </Modal>
  );
}
YesNoModalComponent.propTypes = {
  closeModal: PropTypes.func.isRequired,
  confirmModal: PropTypes.func.isRequired,
  yesButtonText: PropTypes.string,
  noButtonText: PropTypes.string,
  showModal: PropTypes.bool,
  header: PropTypes.string,
  description: PropTypes.string,
};
YesNoModalComponent.defaultProps = {
  yesButtonText: 'Confirm',
  noButtonText: 'Cancel',
  showModal: false,
};
