import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Styles from './ScheduleBanner.module.css';
import SelectComponent from '../../../shared/elements/select/select.component';
import CustomDateRangeInput from '../../../shared/components/custom-date-range/CustomDateRangeInput';

function ScheduleBannerComponent({
  componentName,
  channels,
  callbackSelectChannelFunction,
  callbackSelectMonthFunction,
  callbackSelectYearFunction,
  callbackGenerateSchedule,
  callbackCheckPriorityList,
  disabledGenerate,
  defaultSelected,
  onChangeFormSetScheduleSessionParameters,
  hiddenSelectChannels,
  callbackCreateSchedule,
  isLoading,
  callbackSelectPeriod,
  disabledCreateSchedule,
  setPeriodFilterStartRange,
  setPeriodFilterEndRange,
  selectedPeriod,
  setPeriod,
}) {
  const [selectedChannel, setSelectedChannel] = useState(
    'Please select a channel'
  );

  const [selectedMonth, setSelectedMonth] = useState('Select month');
  const [selectedYear, setSelectedYear] = useState('Select year');
  const currentDate = new Date();
  const [value, setValue] = useState({
    start: new Date(
      currentDate.getFullYear(),
      currentDate.getMonth(),
      currentDate.getDate()
    ),
    end: new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, 0),
  });

  const range = {
    start: new Date(
      currentDate.getFullYear(),
      currentDate.getMonth(),
      currentDate.getDate()
    ),
    end: new Date(
      currentDate.getFullYear() + 3,
      currentDate.getMonth(),
      currentDate.getDate()
    ),
  };
  const [disabledCreate, setDisabledCreate] = useState(true);
  const [runTime, setRuntime] = useState(0);

  useEffect(() => {
    if (runTime < 1) {
      setRuntime(runTime + 1);
      setPeriodFilterStartRange(value.start);
      setPeriodFilterEndRange(value.end);
    }
  }, [runTime]);

  useEffect(() => {
    if (
      selectedChannel !== 'Please select a channel' &&
      selectedMonth !== 'Select month' &&
      selectedYear !== 'Select year'
    ) {
      callbackCheckPriorityList();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedChannel, selectedMonth, selectedYear]);

  useEffect(() => {
    setSelectedChannel(defaultSelected.channel);
    setSelectedMonth(defaultSelected.month);
    setSelectedYear(defaultSelected.year);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [defaultSelected]);

  useEffect(() => {
    if (
      !(
        selectedPeriod && // 👈 null and undefined check
        Object.keys(selectedPeriod).length === 0 &&
        Object.getPrototypeOf(selectedPeriod) === Object.prototype
      )
    ) {
      setValue({
        start: selectedPeriod.start,
        end: selectedPeriod.end,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedPeriod]);

  useEffect(() => {
    setDisabledCreate(disabledCreateSchedule);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [disabledCreateSchedule]);

  useEffect(() => {
    let disabled = false;
    if (value.start < range.start) {
      disabled = true;
    }
    if (value.end > range.end) {
      disabled = true;
    }
    if (selectedChannel === 'Please select a channel') {
      disabled = true;
    }
    if (!value.start || !value.end) {
      disabled = true;
    }
    if (value.start > value.end) {
      disabled = true;
    }
    setDisabledCreate(disabled || disabledCreateSchedule);
    if (!(disabled || disabledCreateSchedule)) {
      setPeriod(value);
      if (value.start && value.end)
        onChangeFormSetScheduleSessionParameters({
          periodFrom: value.start,
          periodTo: value.end,
        });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [value, selectedChannel]);

  const handleSelectChannel = (selected) => {
    callbackSelectChannelFunction(selected.id);
    setSelectedChannel(selected.name);
    onChangeFormSetScheduleSessionParameters({ channelID: selected.id });
  };

  const handleChange = (value) => {
    setValue(value);
  };

  return (
    <div className={`${Styles.header} position-relative`}>
      <div className={Styles.headerText} style={{ width: '20%' }}>
        {componentName}
      </div>
      <div style={{ width: '80%' }}>
        <div
          className={`row ${Styles.marginBottom}`}
          style={{ float: 'right' }}
        >
          <>
            <div className={Styles.divHeaderPadding}>
              <div className={Styles.channelLabel}>Channel:</div>
            </div>
            <div
              className={Styles.divHeaderSelect}
              style={{
                width: '175px',
                visibility: hiddenSelectChannels ? 'hidden' : 'visible',
              }}
            >
              <SelectComponent
                text={selectedChannel}
                list={channels}
                onSelect={handleSelectChannel}
                width="175px"
                withSearch
              />
            </div>
            <>
              <div
                className={`${Styles.divHeaderPadding} ${Styles.paddingTopNone} ${Styles.customDateInput}`}
              >
                {!isLoading && (
                  <CustomDateRangeInput
                    onChange={handleChange}
                    min={range.start}
                    max={range.end}
                    startSettings={{
                      value: value.start,
                      label: 'Period from:',
                      labelLeft: true,
                      width: '155px',
                    }}
                    endSettings={{
                      value: value.end,
                      label: 'To:',
                      labelLeft: true,
                      width: '155px',
                    }}
                  />
                )}
              </div>
            </>
          </>
          <div
            style={{
              width: 'auto',
              padding: '0',
              paddingRight: '10px',
              marginBottom: '10px',
            }}
          >
            <button
              className={`${Styles.addButton} `}
              onClick={callbackGenerateSchedule}
              disabled={
                !(value.start && value.end) ||
                disabledGenerate ||
                value.start > value.end ||
                disabledCreate
              }
              type="button"
            >
              Generate Schedules
            </button>{' '}
          </div>

          <div
            style={{
              width: 'auto',
              padding: '0',
              paddingRight: '10px',
              marginBottom: '10px',
            }}
          >
            <button
              className={`${Styles.addButton} `}
              type="button"
              onClick={callbackCreateSchedule}
              disabled={disabledCreate}
            >
              Create Schedule
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
ScheduleBannerComponent.propTypes = {
  componentName: PropTypes.string.isRequired,
  channels: PropTypes.arrayOf(PropTypes.shape),
  callbackSelectChannelFunction: PropTypes.func.isRequired,
  callbackSelectMonthFunction: PropTypes.func.isRequired,
  callbackSelectYearFunction: PropTypes.func.isRequired,
  callbackGenerateSchedule: PropTypes.func.isRequired,
  callbackCheckPriorityList: PropTypes.func.isRequired,
  disabledGenerate: PropTypes.bool,
  defaultSelected: PropTypes.shape,
  onChangeFormSetScheduleSessionParameters: PropTypes.func.isRequired,
  hiddenSelectChannels: PropTypes.bool,
};
ScheduleBannerComponent.defaultProps = {
  channels: null,
  disabledGenerate: true,
  defaultSelected: null,
  hiddenSelectChannels: true,
};
export default ScheduleBannerComponent;
